<template>
  <div class="cart">
    <div class="cart-header">
      <div class="cart-header-title">
        购物清单
      </div>
      <div class="cart-header-main">
        <div class="cart-info">商品信息</div>
        <div class="price">单价</div>
        <div class="cart-count">数量</div>
        <div class="cart-cost">小计</div>
        <div class="cart-delete">删除</div>
      </div>
    </div>

    <div class="cart-content">
      <div class="cart-content-main" v-for="(item,index) in cartList" :key="index">
        <div class="cart-info">
          <img :src="productDictList[item.id].image" alt="">
          <span>{{productDictList[item.id].name}}</span>
        </div>
        <div class="cart-price">
            ￥{{productDictList[item.id].cost}}
        </div>
        <div class="cart-count">
            <span class="cart-control-minus" @click="handleCount(index,-1)">-</span>
            {{item.count}}
            <span class="cart-control-add" @click="handleCount(index,1)">+</span>
        </div>
        <div class="cart-cost">
            ￥{{productDictList[item.id].cost*item.count }}
        </div>
        <div class="cart-delete">
            <span class="cart-control-delete" @click.prevent="handleDelete(index)">删除</span>
        </div>
      </div>
    </div>

    <div class="cart-promotion">
      <span>使用优惠码：</span>
      <input type="text" v-model="promotionCode">
      <el-button class="cart-control-promotion" @click.prevent="handleCheckCode">
        验证
      </el-button>
      <div class="cart-footer">
        <div class="cart-footer-desc">
          共计<span>{{ countAll }}</span>商品
        </div>
        <div class="cart-footer-desc">
          应付总额<span>￥{{costAll-promotion}}</span>
          <br>
          <template v-if="promotion">
            (优惠<span>￥{{promotion}}</span>)
          </template>
        </div>
        <div class="cart-footer-desc">
          <el-button class="cart-control-order" @click.prevent="handleOrder">
            现在结算
          </el-button>
        </div>

      </div>
    </div>

  </div>
</template>

<script>
import product_data from '../../mock/product.js'
export default{
  data(){
    return{
        productList:product_data,
        promotionCode:'',
        promotion:0
    }
  },
  methods:{
    handleCount(index,count){
        if(count<0 && this.cartList[index].count===1){
          this.handleDelete(index);
          return;
        }
        this.$store.commit('editCartCount',{
          id:this.cartList[index].id,
          count:count
        })
    },
    handleDelete(index){
        this.$store.commit('deleteCart',this.cartList[index].id);
    },
    // 验证优惠码，我们用Vue.js代表正确的优惠码
    handleCheckCode(){
      if(this.promotionCode===''){
        alert('请输入优惠码！');
        return;
      }
      if(this.promotionCode!=='Vue.js'){
        alert('优惠码验证失败');
      }else{
        this.promotion=500;
      }
    },
    // 通知Vuex，完成下单
    handleOrder(){
      this.$store.dispatch('buy').then(()=>{
        alert('购买成功');
      })
    }


  },
  mounted() {

  },
  computed:{
    cartList(){
      return this.$store.state.cartList;
    },
    productDictList(){
      const dict={};
      this.productList.forEach(item=>{
        dict[item.id]=item;
      });
      return dict;
    },
    countAll(){
      return this.cartList.length;
    },
    costAll(){
      let cost=0;
      this.cartList.forEach(item=>{
        cost+=this.productDictList[item.id].cost*item.count;
      });
      return cost;
    }

  }

}
</script>

<style scoped lang="less">
.cart{
  color: black;
  margin: 32px;
  background: #fff;
  border: 1px solid #dddee1;  border-radius: 10px 10px 0 0;
  .cart-header-title{
    padding: 16px 32px;
    border-bottom: 1px solid #dddee1;
    border-radius: 10px 10px 0 0;
    background: #f8f8f9;
  }
  .cart-header-main{
    padding: 8px 32px;
    overflow: hidden;
    border-bottom: 1px solid #dddee1;
    background: #eee;
    div{
      text-align: center; float: left; font-size: 16px;
    }
  }
  .cart-info{
    width: 60%;
  }
  .cart-price,.cart-count,.cart-cost,.cart-delete{
    width: 10%;
  }

}
.cart-content-main{
  padding: 0 32px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  border-bottom: 1px solid #dddee1;
  div{
    float: left;
  }
  img{
    width: 40px; height: 40px;
    position: relative;
    top: 10px; right:6px
  }
  .cart-control-minus,.cart-control-add{
    display: inline-block;
    margin: 0 4px;
    width: 24px; height: 24px;
    line-height: 22px;
    text-align: center;
    background: #f8f8f9;
    border-radius: 50%;
    box-shadow: 0 1px 1px rgba(0,0,0,.2);
    cursor: pointer;
  }
  .cart-control-delete{
    cursor: pointer;
    color: #2d8cf0;
  }
}
</style>
